import { IUseCase } from "../../../../shared/application/use-case.interface";
import { Uuid } from "../../../../shared/domain/value-objects/uuid.vo";
import { ITenantRepository } from "../../../domain/tenant.repository";
import { DeleteTenantInput } from "./delete-tenant.input";

export class DeleteTenantUseCase implements IUseCase<DeleteTenantInput, DeleteTenantOutput>{

  constructor(private readonly tenantRepo: ITenantRepository) { }
  
  async execute(input: DeleteTenantInput): Promise<DeleteTenantOutput> {
    const uuid = new Uuid(input.id);
    await this.tenantRepo.delete(uuid);
    
  }
}

export type DeleteTenantOutput = void;